import NavigationBar from "@/components/common/NavigationBar.vue"
import T from "@/utils/tips.js"
export default {
	data() {
		return {
			title:'选择身份',
			isBack: false,
			items:[
				{
					text:'我是代办',
					imgUrl:'../../../static/imgs/icon-1.png'
				},
				{
					text:'我要卖货',
					imgUrl:'../../../static/imgs/icon-2.png'
				},
				{
					text:'我要采购',
					imgUrl:'../../../static/imgs/icon-3.png'
				},
			]
		};
	},
	components:{ NavigationBar },
	onBackPress(){
		T.tips('请选择角色')
		return false
	},
	onLoad() {
		
	},
	onShow() {
		
	},
	methods:{
		goPage(index){
			switch (index){
				case 0:
				// 代办
					uni.navigateTo({
						url:'/pages/middle/identity/realname/agency?hasfrom=1'
					})
					break;
				case 1:
				// 货主
					uni.navigateTo({
						url:'/pages/middle/identity/realname/agency?hasfrom=2'
					})
					break;	
				case 2:
					uni.navigateTo({
						url:'/pages/middle/identity/realname/buyer'
					})
					break;
				default:
					break;
			}
			
		}
	}
}
